import React, { Component, PropTypes } from 'react'

export class Header extends Component {
  constructor(props) {
    super(props)
    this.state = { isMounted: false }
  }

  render(props, ctrl, children) {
    return (
      <header
        data-0="background-color: rgba(255,255,255,0.50);"
        data-150="background-color: rgba(252, 252, 252, 0);"
      >
        <ul
          id="nav"
          data-0="padding:20px 0;"
          data-150="padding: 0 0;"
        >
          <li><a href="#home" data-anchor-target="#home" data-bottom-top="@class:inactive" data-50-top="@class:active" data-50-top-bottom="@class:inactive"></a></li>
          <li><a href="#about" data-anchor-target="#about" data-bottom-top="@class:inactive" data-50-top="@class:active" data-50-top-bottom="@class:inactive"></a></li>
          <li><a href="#project" data-anchor-target="#project" data-bottom-top="@class:inactive" data-50-top="@class:active" data-50-top-bottom="@class:inactive"></a></li>
          <li><a href="#contact" data-anchor-target="#contact" data-bottom-top="@class:inactive" data-50-top="@class:active" data-50-top-bottom="@class:inactive"></a></li>
        </ul>
      </header>
    )
  }
}


export default Header



