import React, { Component, PropTypes } from 'react'

import ProjectIcon from '../../components/ProjectIcon'
import projectOne from '../../../assets/images/background-2.jpg'

export class Project extends Component {
  constructor(props) {
    super(props)
    this.state = { isMounted: false }
  }

  render(props, ctrl, children) {
    return (
      <div id="project" className="project-section">
        <div
          className="background picture-rose"
          data-center="background-position: 50% 0px;"
          data-top-bottom="background-position: 50% -200px;"
          data-anchor-target="#project"
        >
          <div className="container">
            <div
              data-top="opacity: 1;"
              data-100-center="opacity: 0;"
            >
              <div className="title"></div>
            </div>
            <div
              className="content"
              data-top="opacity: 1;"
              data-100-top="opacity: 0;"
              data-anchor-target="#project .title"
            >
              <div className="projects">
                <ProjectIcon image={projectOne} />
                <ProjectIcon image={projectOne} />
                <ProjectIcon image={projectOne} />
                <ProjectIcon image={projectOne} />
                <ProjectIcon image={projectOne} />
                <ProjectIcon image={projectOne} />
                <ProjectIcon image={projectOne} />
                <ProjectIcon image={projectOne} />
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}


export default Project



