import React, { Component, PropTypes } from 'react'
import TextField from 'material-ui/TextField';

const style = {
  width: '95%',
  marginLeft: '10px',
}

export class Contact extends Component {
  constructor(props) {
    super(props)
    this.state = { isMounted: false }
  }

  render(props, ctrl, children) {
    return (
      <section id="contact">
        <div
          className="background background-contact"
          data-center="background-position: 50% 0px;"
          data-top-bottom="background-position: 50% -200px;"
          data-anchor-target="#contact"
        >
          <div className="container">
            <div
              data-top="opacity: 1;"
              data-100-center="opacity: 0;"
            >
              <div className="title"></div>
            </div>
            <div
              className="contact-form"
              data-100-top="opacity: 1;"
              data-200-center="opacity: 0;"
            >
              <div className="contact-information">
                <div className="contact-fields">
                  <TextField floatingLabelText="" style={ style } />
                  <TextField floatingLabelText="" style={ style } />
                  <TextField floatingLabelText="" style={ style } />
                  <TextField floatingLabelText="" multiLine style={ style } rows="5" />
                </div>
              </div>
              <div className="contact-map">
              </div>
            </div>
          </div>
        </div>
      </section>
    )
  }
}


export default Contact



